/* rucksack alias */
@alias{
    fs: font-size;
    bg:background-color;
    
}

*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

/* body{
    background:red;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    height:100vh;
    width:100%;
} */
body{
    bg:red;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    height:100vh;
    width:100%;
}
p#para{
    /* rucksack font-size */
    fs: responsive;
}
section{
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    height:100vh;
    width:100%;
    border:2px solid #333;
}
h1{
    /* rucksack position */
    position: fixed 20px 20px; 
}
.mouse-actual,.textchange{
    height:100%;
    width:inherit;
    text-align: center;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    padding:16px;


}
.textchange{
    border:2px solid #fff;
}

